/* @jsx jsx */
import {jsx} from "@emotion/react"

import * as React from "react"
import {Helmet} from "react-helmet"
import {graphql, Link} from "gatsby"

import PageLayout from "../components/PageLayout";
import TimelinePost from "../components/LandingPageTimeline/TimelinePost";
import LandingPageHeader from "../components/LandingPageHeader";

import * as styles from "../styles/LandingPage.styles";
import {spacing24, spacing8} from "../styles";

interface IndexPageProps {
    data: {
        allMarkdownRemark: {
            edges: {
                node: {
                    frontmatter: {
                        slug: string;
                        headerImage: {
                            childImageSharp: {
                                fluid: {
                                    base64: string;
                                    aspectRatio: number;
                                    src: string;
                                    srcSet: string;
                                    sizes: string;
                                }
                            }
                        };
                        pageTitle: string;
                        pageDescription: string;
                        headerImageAlt: string
                        initially_published: string
                        last_modified: string
                        tags: string[]
                    }
                }
            }[]
        }
    }
}


const IndexPage = (props: IndexPageProps) => {
    const {data} = props;

    const header = <LandingPageHeader/>;

    const content = <div>
        <h2>Recently updated</h2>
        <div css={spacing8}/>
        {data.allMarkdownRemark.edges.map(edge => {
            return <div>
                <TimelinePost key={edge.node.frontmatter.slug} node={edge.node}/>
                <div css={spacing24}/>
            </div>
        })}
        {data.allMarkdownRemark.edges.length === 10 &&
            <div css={styles.rightAlign}>
                <Link to={"/recent"}>See more</Link>
            </div>
        }
    </div>

    return (
        <main>
            <Helmet>
                <meta charSet="utf-8"/>
                <meta name="viewport" content="width=device-width, initial-scale=1"/>
                <title>Arshi Annafi</title>
                <meta name="description"
                      content="Arshi Annafi | Software Engineer | A place where I share my journey in software development and technology, showcase projects, and post articles and resources."/>
            </Helmet>
            <PageLayout
                header={header}
                content={content}
            />
        </main>
    )
}

export default IndexPage

export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: {fields: frontmatter___initially_published, order: DESC}, limit: 10) {
      edges {
        node {
          frontmatter {
            slug
            headerImage {
              childImageSharp {
                fluid(maxHeight: 100) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            headerImageAlt
            pageTitle
            pageDescription
            initially_published
            last_modified(formatString: "YYYY-MM-DD")
            tags
          }
        }
      }
    }
  }
`;
