/* @jsx jsx */
import {jsx} from "@emotion/react"

import * as React from "react";
import Img from "gatsby-image";
import {Link} from "gatsby";

import * as styles from "./TimelinePost.styles"

interface TimelinePostProps {
    node: {
        frontmatter: {
            slug: string
            headerImage: {
                childImageSharp: {
                    fluid: {
                        base64: string;
                        aspectRatio: number;
                        src: string;
                        srcSet: string;
                        sizes: string;
                    }
                }
            }
            pageTitle: string
            pageDescription: string
            headerImageAlt: string
            initially_published: string
            last_modified: string
            tags: string[]
        }
    }
}

const TimelinePost = (props: TimelinePostProps) => {
    const {node} = props;
    return <div css={styles.container}>
        <div css={styles.imageContainer}>
            <Img
                fluid={node.frontmatter.headerImage.childImageSharp.fluid}
                alt={node.frontmatter.headerImageAlt}
                style={{width: "150px"}}
                imgStyle={{objectFit: "contain"}}
            />
        </div>
        <div css={styles.contentContainer}>
            <div>
                <Link to={node.frontmatter.slug} key={node.frontmatter.slug}>
                    <b>
                        {node.frontmatter.pageTitle}
                    </b>
                </Link>
            </div>
            <div>
                {node.frontmatter.pageDescription}
            </div>
            <div>
                <small>
                    Updated: {node.frontmatter.initially_published}.
                </small>
            </div>
            <div>
                <small>
                    Tags: {" "}
                    {node.frontmatter.tags.map(tag => {
                        return <Link to={`/tags/${tag}`} css={styles.tagLink}
                                     key={`${node.frontmatter.slug}/tag/${tag}`}>
                            {tag}
                        </Link>
                    })}
                </small>
            </div>
        </div>
    </div>
}

export default TimelinePost;
