/* @jsx jsx */
import {jsx} from "@emotion/react";

import * as React from "react";
import * as styles from "./LandingPageHeader.styles";
import Socials from "../Socials";
import {spacing8} from "../../styles";

const showNameCorrection = false;

const CorrectedNameHeader = (): JSX.Element => {
    return <div css={styles.headerWrapper}>
        <div css={styles.headerSection2Wrapper}>
            <div>
                <h1 css={styles.remove}>- Arshi <span css={styles.removedWord}>A</span>nnafi</h1>
                <h1 css={styles.added}>+ Arshi <span css={styles.addedWord}>Abda</span>nnafi</h1>
            </div>
        </div>
        <h2 css={styles.center}>Software Engineer</h2>
    </div>
}

const LandingPageHeader = (): JSX.Element => {

    let header = <div css={styles.headerSection}>
        <img
            src="https://avatars.githubusercontent.com/u/19654498"
            alt="Portrait image of Arshi"
            css={styles.avatarImage}
        />
        <h1>Arshi Annafi</h1>
        <h2>Software Engineer</h2>
        <div css={spacing8}/>
        <Socials />
    </div>

    if (showNameCorrection) {
        header = <CorrectedNameHeader/>
    }

    return header;
}

export default LandingPageHeader;
